body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
}

.btn, .form-control {
  border-radius: 2px;
  font-weight: 300;
  padding: 5px;
}

.btn-primary {
  background-color: #02B8B2;
  border-color: #02B8B2;
}

.btn-primary:hover {
  background-color: #90E0DF;
  border-color: #90E0DF;
}

.btn-secondary {
  background-color: transparent;
  border: solid 2px;
  border-color: white;
  color: #2c2c2d; 
}

.btn-secondary:hover {
  background-color: #eee;
  border-color: #eee;
}

.text-shadow {
  text-shadow: 0 2px 4px rgba(0,0,0,0.2);
}

.card {
  border-radius: 2px;
}
.display-4 {
  font-weight: 100;
}

.lead {
  font-weight: 100;
  font-size: 17px;
  line-height: 30px;
  letter-spacing: 0.13ch;
}

.w-440 {
  max-width: 440px;
}

.w-720 {
  max-width: 720px;
}

.card-shadow {
  -webkit-box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 2px 4px rgba(0, 0, 0, 0.23);
          box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16), 0 2px 4px rgba(0, 0, 0, 0.23);
}

.bg-darker {
  background-color: #2c2c2d;
}

.text-primary {
  color: #02B8B2 !important;
}

.d-inherit{
  display: inherit;
}

/* Calendly */
.calendly-overlay{
  background-color: rgba(31,31,31,0.6);
}
.calendly-badge-widget {
  position: fixed;
  right: 100px;
  bottom: 25px;
  z-index: 9998;
}
.calendly-badge-widget .calendly-badge-content {
  display: table-cell;
  width: auto;
  height: 45px;
  padding: 0 30px;
  border-radius: 25px;
  -webkit-box-shadow: rgba(0,0,0,0.25) 0 2px 5px;
          box-shadow: rgba(0,0,0,0.25) 0 2px 5px;
  font-family: 'Roboto', sans-serif;
  text-transform: uppercase;
  text-align: center;
  vertical-align: middle;
  font-size: 14px;
  font-weight: 400;
  color: #fff;
  cursor: pointer;
}
.rdw-editor-main{
  min-height: 150px !important;
}


